import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'timeago'
})
export class TimeagoPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let d = new Date(value);
    let now = new Date();
    let seconds = Math.round(Math.abs((now.getTime() - d.getTime()) / 1000));
    console.log('second: ',seconds)
    let timeToUpdate = (Number.isNaN(seconds)) ? 1000 : this.getSecondsUntilUpdate(seconds) * 1000;
    let minutes = Math.round(Math.abs(seconds / 60));
    let hours = Math.round(Math.abs(minutes / 60));
    let days = Math.round(Math.abs(hours / 24));
    let months = Math.round(Math.abs(days / 30.416));
    let years = Math.round(Math.abs(days / 365));
    if (Number.isNaN(seconds)) {
      return;
    }
    else if (seconds <= 45) {
      return 'ang hot ng';
    }
    else if (seconds <= 90) {
      return 'Hot ng 1 pht trc';
    }
    else if (minutes <= 45) {
      return 'Hot ng ' + minutes + ' pht trc';
    }
    else if (minutes <= 90) {
      return 'Hot ng 1 gi trc';
    }
    else if (hours <= 22) {
      return 'Hot ng ' + hours + ' gi trc';
    }
    else if (hours <= 36) {
      return 'Hot ng 1 ngy trc';
    }
    else if (days <= 25) {
      return 'Hot ng ' + days + ' ngy trc';
    }
    else if (days <= 45) {
      return 'Hot ng 1 thng trc';
    }
    else if (days <= 345) {
      return 'Hot ng ' + days + ' thng trc';
    }
    else if (days <= 545) {
      return 'Hot ng 1 nm trc';
    }
    else {
      // (days > 545)
      return years + ' years ago';
    }
  }

  getSecondsUntilUpdate = function (seconds) {
    var  min = 60;
    var  hr = min * 60;
    var  day = hr * 24;
    if (seconds < min) {
      // less than 1 min, update every 2 secs
      return 2;
    }
    else if (seconds < hr) {
      // less than an hour, update every 30 secs
      return 30;
    }
    else if (seconds < day) {
      // less then a day, update every 5 mins
      return 300;
    }
    else {
      // update every hour
      return 3600;
    }
  };

}
